import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CreateCustomerComponent } from './create-customer.component';
import { CreateCustomerRoutingModule } from './create-customer-routing';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ImageUploadModule } from 'ng2-imageupload';
import { CommonComponentsModule } from 'common/common.module';
import { PlaceAutoCompleteModule } from 'common/placeautocomplete/placeautocomplete.module';
import { Ng2CompleterModule } from 'ng2-completer';

@NgModule({
  imports: [
    CommonModule,
    CreateCustomerRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    ImageUploadModule,
    CommonComponentsModule,
    PlaceAutoCompleteModule,
    Ng2CompleterModule
  ],
  
  declarations: [CreateCustomerComponent]
})
export class CreateCustomerModule { }



